import IAnkiNote from '../models/IAnkiNote';
import IAnkiRequestNote from '../models/IAnkiRequestNote';
export default class AnkiApi {

    static invoke(action: any, version: any, params={}) {
        return new Promise((resolve, reject) => {
            const xhr = new XMLHttpRequest();
            xhr.addEventListener('error', () => reject('failed to issue request'));
            xhr.addEventListener('load', () => {
                try {
                    const response = JSON.parse(xhr.responseText);
                    if (Object.getOwnPropertyNames(response).length != 2) {
                        throw 'response has an unexpected number of fields';
                    }
                    if (!response.hasOwnProperty('error')) {
                        throw 'response is missing required error field';
                    }
                    if (!response.hasOwnProperty('result')) {
                        throw 'response is missing required result field';
                    }
                    if (response.error) {
                        throw response.error;
                    }
                    resolve(response.result);
                } catch (e) {
                    reject(e);
                }
            });
    
            xhr.open('POST', "http://ec2-107-23-129-31.compute-1.amazonaws.com:8080");
            xhr.setRequestHeader("Accept", "application/json");
            xhr.setRequestHeader("Access-Control-Allow-Origin", "*");
            xhr.setRequestHeader("Host", "ec2-107-23-129-31.compute-1.amazonaws.com:8080");
            xhr.setRequestHeader("User-Agent", "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/78.0.3904.108 Safari/537.36");
            xhr.setRequestHeader("cache-control", "no-cache");
            xhr.send(JSON.stringify({action, version, params}));
        });
    }


    static async addNote({ Word, LexicalCategory, Meaning, Example, Audio, Image}: IAnkiNote){
        const audioRequest = Audio.includes("http") ? {
            "url": Audio,
            "filename": `${Audio}_${LexicalCategory}`,
            "fields": [
                "Audio"
            ]
        }: null;

        let noteObject: IAnkiRequestNote = {
            "deckName": "English season 1 vocabulary",
            "modelName": "Professional-English",
            "fields": {
                "Word": `${Word} ${LexicalCategory}`,
                "LexicalCategory": LexicalCategory,
                "Meaning": Meaning,
                "Example": Example,
                "Audio": Audio.includes("http") ? "" : Audio  ,
                "Image": Image
            },
            "options": {
                "allowDuplicate": false
            },
            "tags": [
                "Flashford"
            ]
        }

        if(audioRequest){
            noteObject = {
                ...noteObject,
                "audio": audioRequest
            }
        }

        const request = {
            "action": "addNote",
            "version": 6,
            "params": {
                "note": noteObject
            }
        }

                
        const result = await AnkiApi.invoke(request.action,request.version, request.params);
        console.log(`Note added: ${result}`);
    }
}